@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200..800&display=swap');

:root {
  --border-radius: 15px;
  --font-base-size: 16px;
  --font-base-line-height: 1.6;
  --font-base-font-weight: 400;
  --font--family: 'Manrope';
  --transition: all 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
  --universal-space: 15px;
  --universal-space-2: calc(var(--universal-space) * 2);
  --universal-space-3: calc(var(--universal-space) * 3);
  --universal-space-4: calc(var(--universal-space) * 4);
  --universal-space-half: calc(var(--universal-space) / 2);
  --c-white: #ffffff;
  --c-black: #020000;
  --c-theme-primary: #ff00ff;
  --c-off-black: #1d2327;
  --c-dark-blue: #000056;
  --c-light-blue: #0000ff;
  --c-error: #ff3b3b;
  box-sizing: border-box;
}

.dmn-widget-root {
  @import 'css-reset-and-normalize/scss/reset-and-normalize';
  font-size: var(--font-base-size);
  max-width: 960px !important;

  :where(*, *::before, *::after) {
    font-size: inherit;
  }

  * {
    box-sizing: border-box;
  }

  @import 'components/accordion';
  @import 'components/date';
  @import 'components/details';
  @import 'components/error-message';
  @import 'components/loading-animation';
  @import 'components/package-card';
  @import 'components/party-size';
  @import 'components/radio';
  @import 'components/review';
  @import 'components/venues';
  @import 'components/steps';
  @import 'components/time';
  @import 'components/type-card';
  @import 'components/typography';
  @import 'components/widget';
}
